import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { I18nModuleResolver } from '@nequi/features/commons';

import { RescheduleLoanMiddleware } from '../../middlewares/reschedule-loan.middleware';
import { RescheduleLoanConfig } from './config/reschedule-loan.config';
import {
  ConditionsRescheduleLoanPage
} from './pages/conditions-reschedule-loan/conditions-reschedule-loan.page';
import { RescheduleLoanHomePage } from './pages/reschedule-loan-home/reschedule-loan-home.page';

const routes: Routes = [
  {
    path: '',
    canActivate: [RescheduleLoanMiddleware],
    component: RescheduleLoanHomePage,
  },
  {
    path: 'home',
    component: RescheduleLoanHomePage,
  },
  {
    path: 'conditions',
    component: ConditionsRescheduleLoanPage,
  },
];

@NgModule({
  imports: [
    RouterModule.forChild(I18nModuleResolver.setup(routes, [RescheduleLoanConfig.i18n.moduleKey])),
  ],
  exports: [RouterModule],
})
export class RescheduleLoanRoutingModule {}
