import { CurrencyPipe, DatePipe } from '@angular/common';
import { Component, Injector, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CoreException } from '@nequi/commons/exceptions';
import {
  AnalyticsInteractor,
  AnalyticsType,
  OperationType,
  Transaction
} from '@nequi/features/commons/core';
import { DynatraceService, TransactionBasePage } from '@nequi/features/commons/presentation';
import {
  CannotRescheduleLoanException,
  DuplicateTransactionException,
  ExceedsMaximumTermException,
  LoanRescheduleParam,
  LoanRescheduleRepository,
  PreviousRequestInProcessException,
  TimeoutRescheduleLoanException
} from '@nequi/features/credits/core';
import { CardModalService, ConfirmInfoBottomSheetService, ConfirmInfoPocketProps } from '@nequi/ui';
import { getFormattedDateTimes } from '@nequi/utils/date';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';
import { createFormat } from '@nequi/utils/string';

import { CreditLoggerService } from '../../../../services/credit-logger.service';
import {
  RescheduleLoanInfo,
  RescheduleLoanInfoModel
} from '../../services/reschedule-loan-info.model';
import { ConditionsRescheduleLoanConfig } from './conditions-reschedule-loan.config';

@Component({
  selector: 'app-credits-conditions-reschedule-loan',
  templateUrl: './conditions-reschedule-loan.page.html',
  styleUrls: ['./conditions-reschedule-loan.page.scss'],
})
export class ConditionsRescheduleLoanPage extends TransactionBasePage implements OnInit {
  public config = ConditionsRescheduleLoanConfig;
  public content: Record<string, string>[];
  private factoryException = {};
  private reschedule: RescheduleLoanInfo;

  constructor(
    public viewModel: RescheduleLoanInfoModel,
    private router: Router,
    private route: ActivatedRoute,
    private readonly cardModalService: CardModalService,
    private readonly currencyPipe: CurrencyPipe,
    private datePipe: DatePipe,
    private readonly confirmInfoBottomSheetService: ConfirmInfoBottomSheetService,
    private analyticsInteractor: AnalyticsInteractor,
    private creditLoggerService: CreditLoggerService,
    private dynatraceService: DynatraceService,
    injector: Injector
  ) {
    super(injector);
    this.setExceptionFactory();
  }

  public ngOnInit(): void {
    this.reschedule = this.router.getCurrentNavigation().extras.state as RescheduleLoanInfo;

    this.viewModel.rescheduleLoanInfo = this.reschedule;
    this.content = this.config.i18n.content[this.reschedule.type];
  }

  public async onConfirm() {
    this.rescheduleLoan();
  }

  protected async onTransactionFailed(error: CoreException) {
    this.canContinue = true;

    this.manageErrors(error);
  }

  protected async onTransactionSuccess(expireAt: Date) {
    await this.loadingService.hide(true);
    await this.setAnalyticsIntentionOfSuccess(expireAt);

    this.onShowCardModal({
      i18nMessage: this.config.i18n.cardModal.extendedYourTerm,
      expireAt: expireAt,
    });
  }

  protected async setOriginMoney(transaction: Transaction): Promise<Transaction> {
    return transaction;
  }

  protected async getConfirmationInfo(): Promise<ConfirmInfoPocketProps> {
    const messages = this.translateProvider.get(this.config.i18n.confirmation);

    const messageRescheduleType = this.translateProvider.get(
      `${this.config.i18n.rescheduleType}.${this.viewModel.rescheduleLoanInfo.type}`
    );

    return {
      title: messages.title,
      subtitle: messages.subtitle,
      items: this.viewModel.buildItems(messageRescheduleType, messages.items, this.currencyPipe),
      confirmLabel: messages.buttons.confirm,
      viewCancel: false,
    };
  }

  protected async showConfirmation(_transaction: Transaction): Promise<boolean> {
    const info = await this.getConfirmationInfo();

    if (!info) {
      return true;
    }

    const { data } = await this.confirmInfoBottomSheetService.show(info);

    return data === true;
  }

  private async rescheduleLoan() {
    this.setLog(this.config.i18n.logs);

    this.canContinue = false;

    const params: LoanRescheduleParam = {
      productId: this.viewModel.rescheduleLoanInfo.productId,
      loanAccount: this.viewModel.rescheduleLoanInfo.debenture,
      nextInstallmentAt: this.viewModel.rescheduleLoanInfo.nextInstallmentAt,
      newInstallment: this.viewModel.rescheduleLoanInfo.totalTerms,
      additionalTerms: this.viewModel.rescheduleLoanInfo.additionalTerms,
      rescheduleType: this.viewModel.rescheduleLoanInfo.type,
    };

    const transactionInfo = new Transaction<LoanRescheduleParam>(OperationType.LOAN_RESCHEDULE)
      .withData(params)
      .useRepository(LoanRescheduleRepository.name);

    super.executeTransaction<LoanRescheduleParam>(transactionInfo);
  }

  private async onShowCardModal(params: {
    i18nMessage: string;
    expireAt?: Date;
    maxTerm?: string;
  }): Promise<void> {
    const message = this.translateProvider.get(params.i18nMessage);
    const content = await this.contentModal({
      ...params,
      content: message?.content ?? message?.description,
    });

    await this.cardModalService.show({
      title: message.title,
      imgPath: message.image,
      content: content,
      buttons: [
        {
          text: message.buttons.accept,
          handler: async () => this.goToRoute(this.config.routes.backpack),
        },
      ],
    });
  }

  private async contentModal(params: {
    expireAt?: Date;
    maxTerm?: string;
    content: string;
  }): Promise<string> {
    if (params?.expireAt) {
      return createFormat(params.content, [this.datePipe.transform(params?.expireAt)]);
    }

    if (params?.maxTerm) {
      return createFormat(params.content, [params?.maxTerm]);
    }

    return createFormat(params.content, [this.viewModel.creditName]);
  }

  private goToRoute(route: string) {
    this.navigatorProvider.navigateRoot(route, {
      relativeTo: this.route,
      replaceUrl: true,
    });
  }

  private setExceptionFactory() {
    this.factoryException = {
      [TimeoutRescheduleLoanException.name]: async () => {
        await this.loadingService.hide(true);
        this.onShowCardModal({ i18nMessage: this.config.i18n.cardModal.timeoutRescheduleLoan });
      },
      [PreviousRequestInProcessException.name]: async () => {
        await this.loadingService.hide(true);
        this.onShowCardModal({ i18nMessage: this.config.i18n.cardModal.previousRequestInProcess });
      },
      [DuplicateTransactionException.name]: () =>
        this.onShowCardModal({ i18nMessage: this.config.i18n.cardModal.duplicateTransaction }),
      [CannotRescheduleLoanException.name]: () =>
        this.onShowCardModal({ i18nMessage: this.config.i18n.cardModal.cannotRescheduleLoanError }),
      [ExceedsMaximumTermException.name]: (error) =>
        this.onShowCardModal({
          i18nMessage: this.config.i18n.cardModal.exceedsMaximumTermError,
          maxTerm: error.data?.maxTerm,
        }),
    };
  }

  private async manageErrors(error: CoreException) {
    if (
      error.constructor.name !== PreviousRequestInProcessException.name &&
      error.constructor.name !== TimeoutRescheduleLoanException.name
    ) {
      await this.loadingService.hide(false);
    }

    if (this.factoryException[error.constructor.name]) {
      this.factoryException[error.constructor.name](error);

      return;
    }

    await this.toastService.showError(error.message);
  }

  private async setAnalyticsIntentionOfSuccess(expireAt: Date): Promise<void> {
    const eventName = this.config.analytics.intentionOfSuccess.name;

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: eventName,
        eventProps: stringsArrayToObjectWithValues(
          this.config.analytics.intentionOfSuccess.params.cleverTap,
          [
            this.viewModel.rescheduleLoanInfo.productId,
            this.viewModel.rescheduleLoanInfo.type,
            this.viewModel.rescheduleLoanInfo.totalTerms,
            this.viewModel.rescheduleLoanInfo.feeAmount,
          ]
        ),
      },
    });

    await this.dynatraceService.setAnalyticsOfEvent({
      eventName: eventName.toLowerCase(),
      eventProps: stringsArrayToObjectWithValues(
        this.config.analytics.intentionOfSuccess.params.dynatrace,
        [
          this.viewModel.rescheduleLoanInfo.productId,
          this.viewModel.rescheduleLoanInfo.type,
          this.viewModel.rescheduleLoanInfo.totalTerms,
          this.viewModel.rescheduleLoanInfo.feeAmount,
          getFormattedDateTimes(expireAt),
        ]
      ),
      leave: true,
    });
  }

  private async setLog(log) {
    await this.creditLoggerService.save({
      flowType: `${log.name}_${
        this.viewModel.rescheduleLoanInfo.productId
      }|${this.datePipe.transform(new Date(), this.config.formatLogDate)}`,
      i18nKey: this.config.i18n.logs.messages[this.reschedule.type],
    });
  }
}
