import { CurrencyPipe, DatePipe } from '@angular/common';
import { Injectable } from '@angular/core';
import {
  ActivatedRoute,
  ActivatedRouteSnapshot,
  CanActivate,
  Router,
  RouterStateSnapshot
} from '@angular/router';
import { CoreException } from '@nequi/commons/exceptions';
import { OriginFlow } from '@nequi/features/commons';
import { AnalyticsInteractor, AnalyticsType } from '@nequi/features/commons/core';
import { DynatraceService } from '@nequi/features/commons/presentation';
import {
  ModuleConfigProvider,
  NavigatorProvider,
  TranslateProvider
} from '@nequi/features/commons/providers';
import {
  CannotRescheduleLoanException,
  CreditInteractor,
  CreditType,
  DuplicateTransactionException,
  ExceedsMaximumTermException
} from '@nequi/features/credits/core';
import { GetCustomerCreditResponse } from '@nequi/mfp/adapters/credits-services/entities';
import {
  CardModalService,
  FullscreenModalService,
  LoadingService,
  ModalButton,
  ModalButtonColor,
  ToastService,
  ViewDetailStatus
} from '@nequi/ui';
import { stringsArrayToObjectWithValues, toCamelCaseProperties } from '@nequi/utils/functions';
import { createFormat } from '@nequi/utils/string';

import {
  RescheduleLoanHomeConfig
} from '../products/reschedule-loan/pages/reschedule-loan-home/reschedule-loan-home.config';
import {
  RescheduleLoanInfoModel
} from '../products/reschedule-loan/services/reschedule-loan-info.model';
import { CreditInfoModel } from '../services/credit-info.model';
import { MiddlewareModuleName } from './base.middleware';
import { ModalParams } from './modal.params';

@Injectable()
export class RescheduleLoanMiddleware implements CanActivate {
  private config = RescheduleLoanHomeConfig;
  private originFlow: number;
  private factoryException = {};

  constructor(
    private router: Router,
    private creditInteractor: CreditInteractor,
    private loadingService: LoadingService,
    private translateProvider: TranslateProvider,
    private navigatorProvider: NavigatorProvider,
    private cardModalService: CardModalService,
    private rescheduleLoanModel: RescheduleLoanInfoModel,
    private toastService: ToastService,
    private moduleConfigProvider: ModuleConfigProvider,
    private analyticsInteractor: AnalyticsInteractor,
    private creditInfoModel: CreditInfoModel,
    private fullscreenModalService: FullscreenModalService,
    private currencyPipe: CurrencyPipe,
    private datePipe: DatePipe,
    private dynatraceService: DynatraceService,
    private route: ActivatedRoute
  ) {}

  public async canActivate(_route: ActivatedRouteSnapshot, _state: RouterStateSnapshot) {
    await this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey);

    if (!this.config.validPaths.includes(this.router.url)) {
      return false;
    }

    this.setAnalyticsOfBegin();
    this.setExceptionFactory();

    if (await this.validateGetRescheduleLoan()) {
      return this.navigatorProvider.navigateForward(this.config.routes.homeRescheduleLoan);
    }

    return false;
  }

  private async validateGetRescheduleLoan(): Promise<boolean> {
    try {
      await this.loadingService.show();
      this.originFlow = await this.getState();

      const resultGetReschedule = await this.creditInteractor.getRescheduleLoan({
        creditCode: this.rescheduleLoanModel.creditCode,
        productId: this.rescheduleLoanModel.productId,
      });

      this.rescheduleLoanModel.rescheduleOptionList = resultGetReschedule;

      this.getCredit();
      await this.setAnalyticsIntentionOfRelief();
      await this.loadingService.hideWithoutFeedback();

      if (this.originFlow === OriginFlow.CREDITS) {
        return this.showPaymentOptionsModal();
      }

      return true;
    } catch (error) {
      this.processErrors(error);
    } finally {
      await this.dynatraceService.setAnalyticsOfEnd();
    }
  }

  private async getState(): Promise<number> {
    const state = this.router.getCurrentNavigation().extras.state;

    if (state?.originFlow !== OriginFlow.CREDITS) {
      const { loanAcc, productId } = state;

      this.rescheduleLoanModel.creditCode = loanAcc;
      this.rescheduleLoanModel.productId = Number(productId);

      return null;
    }

    this.rescheduleLoanModel.creditCode = this.creditInfoModel.customerCreditInfo.credit.debenture;
    this.rescheduleLoanModel.productId = Number(state.creditType);

    return state.originFlow;
  }

  private async getCredit() {
    switch (this.rescheduleLoanModel.productId) {
      case CreditType.PROPELLER:
        this.getCreditName(MiddlewareModuleName.PROPELLER);
        break;
      case CreditType.LIFEGUARD_RECHARGED:
        this.getCreditName(MiddlewareModuleName.LIFEGUARD_RECHARGED);
        break;
    }
  }

  private getCreditName(credit: MiddlewareModuleName) {
    let creditName = this.translateProvider.get(this.config.i18n.creditName);

    creditName = toCamelCaseProperties<Record<string, string>>(creditName);
    this.rescheduleLoanModel.creditName = creditName[credit];
  }

  private async processErrors(error: CoreException<GetCustomerCreditResponse>) {
    await this.loadingService.hideWithoutFeedback();
    await this.dynatraceService.setCustomError(error);

    if (this.factoryException[error.constructor.name]) {
      this.factoryException[error.constructor.name](error);

      return;
    }

    await this.toastService.showError(error.message);
  }

  private setExceptionFactory() {
    this.factoryException = {
      [CannotRescheduleLoanException.name]: () => this.processCannotRescheduleLoanError(),
      [ExceedsMaximumTermException.name]: (error) => this.processExceedsMaximumTermError(error),
      [DuplicateTransactionException.name]: (error) => this.processDuplicateTransactionError(error),
    };
  }

  private processCannotRescheduleLoanError() {
    let messages = this.translateProvider.get(this.config.i18n.modal.cannotRescheduleLoanError);

    if (this.originFlow === OriginFlow.CREDITS) {
      messages = this.translateProvider.get(this.config.i18n.modal.unapprovedReliefError);
    }

    this.showCardModal({ message: messages } as ModalParams);
  }

  private processExceedsMaximumTermError(error) {
    const messages = this.translateProvider.get(this.config.i18n.modal.exceedsMaximumTermError);

    if (error?.data?.maxTerm) {
      messages.content = createFormat(messages.content, [error.data.maxTerm]);
    }

    this.showCardModal({
      message: messages,
      params: { url: this.config.routes.backpack },
    } as ModalParams);
  }

  private processDuplicateTransactionError(error) {
    const messages = this.translateProvider.get(this.config.i18n.modal.duplicateTransaction);

    if (error?.data?.nextExtensionDate) {
      messages.content = createFormat(messages.content, [
        this.datePipe.transform(error.data.nextExtensionDate, this.config.nextExtensionDateFormat),
      ]);
    }

    this.showCardModal({
      message: messages,
      params: {
        url: this.config.routes.backpack,
      },
    } as ModalParams);
  }

  private showCardModal(modalInfo: ModalParams) {
    const message = modalInfo.i18nKey
      ? this.translateProvider.get(modalInfo.i18nKey)
      : modalInfo.message;

    const buttons: ModalButton[] = [
      {
        text: message.buttons.accept,
        handler: () => {
          if (modalInfo?.params?.url) {
            this.goToRoute(String(modalInfo.params.url));
          }
        },
      },
    ];

    if (message.buttons?.report) {
      buttons.push({
        color: ModalButtonColor.MIDNIGHT,
        text: message.buttons.report,
        handler: () => this.goToReportIssue(),
      });
    }

    this.cardModalService.show({
      title: message.title,
      imgPath: message.image,
      content: message.description ?? message.content,
      buttons: buttons,
    });
  }

  private async setAnalyticsIntentionOfRelief(): Promise<void> {
    const eventName = this.config.analytics.intentionOfRelief.name;

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: eventName,
        eventProps: stringsArrayToObjectWithValues(
          this.config.analytics.intentionOfRelief.params.cleverTap,
          [
            this.rescheduleLoanModel.productId,
            this.rescheduleLoanModel.rescheduleOptionList[0].rescheduleType,
          ]
        ),
      },
    });
  }

  private async showPaymentOptionsModal(): Promise<boolean> {
    const i18n = this.translateProvider.get(this.config.i18n.modal.paymentOptions);
    const i18nFormatted = toCamelCaseProperties<Record<string, string>>(i18n);

    const result = await this.fullscreenModalService.show<{ data: boolean }>({
      title: createFormat(i18nFormatted.title, [this.rescheduleLoanModel.creditName]),
      imgPath: i18nFormatted.image,
      content: i18nFormatted.description,
      cssClass: this.config.fullscreenModal.cssClass,
      buttons: [
        {
          text: i18nFormatted.acceptButton,
          handler: async () => {
            this.fullscreenModalService.hide(true);
          },
          cssClass: this.config.fullscreenModal.cssClassButton,
        },
      ],
    });

    return !!result?.data;
  }

  private goToRoute(route: string) {
    this.navigatorProvider.navigateRoot(route, {
      relativeTo: this.route,
      replaceUrl: true,
    });
  }

  private goToReportIssue() {
    this.getCredit();

    const messageItemDetail = this.translateProvider.get(this.config.i18n.reportDetail);

    const detail = {
      title: createFormat(messageItemDetail.title, [this.rescheduleLoanModel.creditName]),
      status: ViewDetailStatus.RED,
      details: [
        {
          label: messageItemDetail.amount,
          description: this.currencyPipe.transform(
            this.creditInfoModel.customerCreditInfo.credit.approvedValue
          ),
        },
        {
          label: messageItemDetail.date,
          description: this.transformFinishDate(
            this.creditInfoModel.customerCreditInfo.credit.startAt
          ),
        },
        {
          label: messageItemDetail.installments,
          description: String(this.creditInfoModel.customerCreditInfo.credit.installment.total),
        },
      ],
    };

    this.navigatorProvider.navigateForward(this.config.routes.reportProblem, {
      state: {
        detail: detail,
        originFlow: OriginFlow.RESCHEDULE_LOAN,
        routeReturn: this.config.routes.propeller,
      },
    });
  }

  private transformFinishDate(goalFinishDate: Date): string {
    return this.datePipe.transform(goalFinishDate, this.config.finishDateFormat);
  }

  private async setAnalyticsOfBegin() {
    await this.dynatraceService.setAnalyticsOfEvent({
      eventName: this.config.analytics.intentionOfRelief.name.toLowerCase(),
      eventProps: stringsArrayToObjectWithValues(
        this.config.analytics.intentionOfRelief.params.dynatrace,
        [this.rescheduleLoanModel.productId]
      ),
    });
  }
}
