import { Component, Injector, OnInit } from '@angular/core';
import { AnalyticsInteractor, AnalyticsType } from '@nequi/features/commons/core';
import { DynatraceService, FeatureBasePage } from '@nequi/features/commons/presentation';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';

import { RescheduleLoanInfoModel } from '../../services/reschedule-loan-info.model';
import { RescheduleLoanHomeConfig } from './reschedule-loan-home.config';

@Component({
  selector: 'app-credits-reschedule-home-loan',
  templateUrl: './reschedule-loan-home.page.html',
  styleUrls: ['./reschedule-loan-home.page.scss'],
})
export class RescheduleLoanHomePage extends FeatureBasePage implements OnInit {
  public config = RescheduleLoanHomeConfig;

  constructor(
    injector: Injector,
    public viewModel: RescheduleLoanInfoModel,
    private analyticsInteractor: AnalyticsInteractor,
    private dynatraceService: DynatraceService
  ) {
    super(injector);
  }

  public ngOnInit() {
    this.initPage();
  }

  public async ionViewWillLeave() {
    await this.dynatraceService.setAnalyticsOfEnd();
  }

  public async onConfirm() {
    await this.setAnalyticsIntentionOfOption();
    this.goToConditionsRescheduleLoan();
  }

  public loadData() {}

  private async initPage() {
    this.viewModel.initControl();

    await this.dynatraceService.setAnalyticsOfEvent({
      eventName: this.config.analytics.intentionOfOption.name.toLowerCase(),
    });
  }

  private goToConditionsRescheduleLoan() {
    const option = this.viewModel.rescheduleOptionList[this.viewModel.rescheduleOption.value];

    this.navigatorProvider.navigateForward(this.config.routes.conditionsRescheduleLoan, {
      state: {
        productId: this.viewModel.productId,
        debenture: this.viewModel.creditCode,
        nextInstallmentAt: option.nextInstallmentAt,
        totalTerms: option.totalInstallments,
        additionalTerms: option.installments,
        type: option.rescheduleType,
        feeAmount: option.monthlyFee,
      },
    });
  }

  private async setAnalyticsIntentionOfOption(): Promise<void> {
    const eventName = this.config.analytics.intentionOfOption.name;

    const installments = [];
    const monthlyFee = [];

    this.viewModel.rescheduleOptionList.map((item) => [
      installments.push(item.installments),
      monthlyFee.push(item.monthlyFee),
    ]);

    const eventProps = [
      this.viewModel.productId,
      this.viewModel.rescheduleOptionList[0].rescheduleType,
      installments.join('-'),
      monthlyFee.join('-'),
    ];

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: eventName,
        eventProps: stringsArrayToObjectWithValues(
          this.config.analytics.intentionOfOption.params.cleverTap,
          eventProps
        ),
      },
    });

    const dynatraceProps = stringsArrayToObjectWithValues(
      this.config.analytics.intentionOfOption.params.dynatrace,
      eventProps
    );

    this.dynatraceService.setAnalyticsOfProperties(dynatraceProps);
  }
}
