import { CurrencyPipe } from '@angular/common';
import { Injectable } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { CreditType, GetRescheduleLoanEntity, RescheduleType } from '@nequi/features/credits/core';
import { ConfirmInfoPocketItem } from '@nequi/ui';
import { createFormat } from '@nequi/utils/string';

export interface RescheduleLoanInfo {
  productId: CreditType;
  debenture: string;
  nextInstallmentAt: Date;
  totalTerms: number;
  additionalTerms: number;
  type: RescheduleType;
  feeAmount: number;
}

@Injectable({ providedIn: 'root' })
export class RescheduleLoanInfoModel {
  public rescheduleOption: FormControl;
  public rescheduleOptionList: GetRescheduleLoanEntity[];
  public creditCode: string;
  public productId: number;
  public rescheduleLoanInfo: RescheduleLoanInfo;
  public creditName: string;

  public initControl(): void {
    this.rescheduleOption = new FormControl(null, [Validators.required]);
  }

  public buildItems(
    messageRescheduleType: string,
    messages: { [key: string]: Record<string, string> },
    currencyPipe: CurrencyPipe
  ): Array<ConfirmInfoPocketItem> {
    const installments =
      this.rescheduleLoanInfo.totalTerms - this.rescheduleLoanInfo.additionalTerms;

    return [
      {
        label: String(messages.type),
        value: messageRescheduleType,
      },
      {
        label: createFormat(messages.term.label, [this.creditName]),
        value: createFormat(messages.term.value, [String(installments)]),
      },
      {
        label: messages.termExtensionBy.label,
        value: createFormat(messages.termExtensionBy.value, [
          String(this.rescheduleLoanInfo.additionalTerms),
        ]),
      },
      {
        label: createFormat(messages.newTerm.label, [this.creditName]),
        value: createFormat(messages.newTerm.value, [String(this.rescheduleLoanInfo.totalTerms)]),
      },
      {
        label: messages.amount.label,
        value: createFormat(messages.amount.value, [
          currencyPipe.transform(this.rescheduleLoanInfo.feeAmount, '', 'symbol', '.0'),
        ]),
      },
    ];
  }
}
